import { Col, Form, Input, Row, Select, Tooltip } from "antd";
import { InfoCircleOutlined } from "@ant-design/icons";

import React from "react";

const suffixColor = "#aaa";

const KEYBOARD_TYPES = [
  { type: 0, label: "Bn phm thng" },
  { type: 1, label: "Bn phm gi c" },
  { type: 2, label: "Bn phm c" },
];
const KEYBOARD_COLORS = [
  { type: 0, label: "en" },
  { type: 1, label: "Bc" },
  { type: 2, label: "Trng" },
  { type: 3, label: "Hng" },
  { type: 4, label: "Khc" },
];
const KEYBOARD_LED_COLORS = [
  { type: 0, label: "Khng led" },
  { type: 1, label: "n sc" },
  { type: 2, label: "Rainbow" },
  { type: 3, label: "RGB" },
];

function Keyboard() {
  return (
    <Row gutter={[16, 16]}>
      {/* Loi bn phm */}
      <Col span={12} md={8} xl={6} xxl={4}>
        <Form.Item
          name="type"
          rules={[{ required: true, message: "Bt buc" }]}
        >
          <Select size="large" placeholder="Loi bn phm *">
            {KEYBOARD_TYPES.map((item, index) => (
              <Select.Option value={item.type} key={index}>
                {item.label}
              </Select.Option>
            ))}
          </Select>
        </Form.Item>
      </Col>
      {/* Mu bn phm */}
      <Col span={12} md={8} xl={6} xxl={4}>
        <Form.Item
          name="color"
          rules={[{ required: true, message: "Bt buc" }]}
        >
          <Select size="large" placeholder="Mu bn phm *">
            {KEYBOARD_COLORS.map((item, index) => (
              <Select.Option value={item.type} key={index}>
                {item.label}
              </Select.Option>
            ))}
          </Select>
        </Form.Item>
      </Col>
      {/*  kt ni  */}
      <Col span={12} md={8} xl={6} xxl={4}>
        <Form.Item
          name="connect"
          rules={[{ required: true, message: "Bt buc" }]}
        >
          <Input
            size="large"
            placeholder="Kt ni *"
            suffix={
              <Tooltip title="2.4 GHz Wireless">
                <InfoCircleOutlined style={{ color: suffixColor }} />
              </Tooltip>
            }
          />
        </Form.Item>
      </Col>
      {/* Mu n led */}
      <Col span={12} md={8} xl={6} xxl={4}>
        <Form.Item
          name="ledColor"
          rules={[{ required: true, message: "Bt buc" }]}
        >
          <Select size="large" placeholder="Mu n led *">
            {KEYBOARD_LED_COLORS.map((item, index) => (
              <Select.Option value={item.type} key={index}>
                {item.label}
              </Select.Option>
            ))}
          </Select>
        </Form.Item>
      </Col>
      {/*  Kiu switch  */}
      <Col span={12} md={8} xl={6} xxl={4}>
        <Form.Item
          name="typeSwitch"
          rules={[{ required: true, message: "Bt buc" }]}
        >
          <Input
            size="large"
            placeholder="Kiu switch *"
            suffix={
              <Tooltip title="Dare-U D Switch Brown">
                <InfoCircleOutlined style={{ color: suffixColor }} />
              </Tooltip>
            }
          />
        </Form.Item>
      </Col>
      {/*  Phm chc nng  */}
      <Col span={12} md={8} xl={6} xxl={4}>
        <Form.Item
          name="funcKey"
          rules={[{ required: true, message: "Bt buc" }]}
        >
          <Input
            size="large"
            placeholder="Phm chc nng *"
            suffix={
              <Tooltip title="C">
                <InfoCircleOutlined style={{ color: suffixColor }} />
              </Tooltip>
            }
          />
        </Form.Item>
      </Col>
      {/*  Kich thc  */}
      <Col span={12} md={8} xl={6} xxl={4}>
        <Form.Item
          name="size"
          rules={[{ required: true, message: "Bt buc" }]}
        >
          <Input
            size="large"
            placeholder="Kich thc *"
            suffix={
              <Tooltip title="full size">
                <InfoCircleOutlined style={{ color: suffixColor }} />
              </Tooltip>
            }
          />
        </Form.Item>
      </Col>
    </Row>
  );
}

export default Keyboard;
