import { InfoCircleOutlined } from "@ant-design/icons";
import { Col, Form, Input, InputNumber, Row, Tooltip } from "antd";
import React from "react";
const suffixColor = "#aaa";

function Laptop() {
  return (
    <Row gutter={[16, 16]}>
      {/* Mu sc*/}
      <Col span={12} md={8} xl={6} xxl={4}>
        <Form.Item
          name="color"
          rules={[{ required: true, message: "Bt buc", whitespace: true }]}
        >
          <Input
            size="large"
            placeholder="Mu sc *"
            suffix={
              <Tooltip title="en">
                <InfoCircleOutlined style={{ color: suffixColor }} />
              </Tooltip>
            }
          />
        </Form.Item>
      </Col>
      {/*thng hiu chip*/}
      <Col span={12} md={8} xl={6} xxl={4}>
        <Form.Item
          name="generationCpu"
          rules={[{ required: true, message: "Bt buc", whitespace: true }]}
        >
          <Input
            size="large"
            placeholder="Th h Cpu *"
            suffix={
              <Tooltip title=" Core i7 , Intel Core th h th 11">
                <InfoCircleOutlined style={{ color: suffixColor }} />
              </Tooltip>
            }
          />
        </Form.Item>
      </Col>
      {/* chi tit chip cpu */}
      <Col span={12} md={8} xl={6} xxl={4}>
        <Form.Item
          name="detailCpu"
          rules={[{ required: true, message: "Bt buc", whitespace: true }]}
        >
          <Input
            size="large"
            placeholder="Chi tit ca cpu *"
            suffix={
              <Tooltip title="9750H up to 4.5 GHz">
                <InfoCircleOutlined style={{ color: suffixColor }} />
              </Tooltip>
            }
          />
        </Form.Item>
      </Col>
      {/* kch thc mn hnh */}
      <Col span={12} md={8} xl={6} xxl={4}>
        <Form.Item
          name="displaySize"
          rules={[{ required: true, message: "Bt buc", whitespace: true }]}
        >
          <Input
            size="large"
            placeholder="Mn hnh *"
            suffix={
              <Tooltip title={`15.6" (1920 x 1080), 60Hz`}>
                <InfoCircleOutlined style={{ color: suffixColor }} />
              </Tooltip>
            }
          />
        </Form.Item>
      </Col>
      {/* Card mn hnh */}
      <Col span={12} md={8} xl={6} xxl={4}>
        <Form.Item
          name="display"
          rules={[{ required: true, message: "Bt buc", whitespace: true }]}
        >
          <Input
            size="large"
            placeholder="Card mn hnh *"
            suffix={
              <Tooltip title="NVIDIA GeForce RTX 2080 Super 8GB GDDR6">
                <InfoCircleOutlined style={{ color: suffixColor }} />
              </Tooltip>
            }
          />
        </Form.Item>
      </Col>

      {/*  cng */}
      <Col span={12} md={8} xl={6} xxl={4}>
        <Form.Item
          name="disk"
          rules={[{ required: true, message: "Bt buc", whitespace: true }]}
        >
          <Input
            size="large"
            placeholder=" cng *"
            suffix={
              <Tooltip title="1TB SSD M.2 NVMe">
                <InfoCircleOutlined style={{ color: suffixColor }} />
              </Tooltip>
            }
          />
        </Form.Item>
      </Col>
      {/* RAM */}
      <Col span={12} md={8} xl={6} xxl={4}>
        <Form.Item
          name="ram"
          rules={[{ required: true, message: "Bt buc", whitespace: true }]}
        >
          <Input
            size="large"
            placeholder="RAM *"
            suffix={
              <Tooltip title="2 x 16GB DDR4 2933MHz">
                <InfoCircleOutlined style={{ color: suffixColor }} />
              </Tooltip>
            }
          />
        </Form.Item>
      </Col>
      {/* kiu khe M.2 h tr  */}
      <Col span={12} md={8} xl={6} xxl={4}>
        <Form.Item
          name="typeM2Sub"
          rules={[{ required: true, message: "Bt buc", whitespace: true }]}
        >
          <Input
            size="large"
            placeholder="Kiu khe M.2 h tr *"
            suffix={
              <Tooltip title="M.2 SATA/NVMe">
                <InfoCircleOutlined style={{ color: suffixColor }} />
              </Tooltip>
            }
          />
        </Form.Item>
      </Col>
      {/* Cng xut hinh */}
      <Col span={12} md={8} xl={6} xxl={4}>
        <Form.Item
          name="outputPort"
          rules={[{ required: true, message: "Bt buc", whitespace: true }]}
        >
          <Input
            size="large"
            placeholder="Cng xut hinh *"
            suffix={
              <Tooltip title="1 x HDMI">
                <InfoCircleOutlined style={{ color: suffixColor }} />
              </Tooltip>
            }
          />
        </Form.Item>
      </Col>
      {/* Cng kt ni */}
      <Col span={12} md={8} xl={6} xxl={4}>
        <Form.Item
          name="connectionPort"
          rules={[{ required: true, message: "Bt buc", whitespace: true }]}
        >
          <Input
            size="large"
            placeholder="Cng kt ni *"
            suffix={
              <Tooltip title="1 x USB Type C">
                <InfoCircleOutlined style={{ color: suffixColor }} />
              </Tooltip>
            }
          />
        </Form.Item>
      </Col>
      {/*  Kt ni khng dy */}
      <Col span={12} md={8} xl={6} xxl={4}>
        <Form.Item
          name="connectWireless"
          rules={[{ required: true, message: "Bt buc", whitespace: true }]}
        >
          <Input
            size="large"
            placeholder="Kt ni khng dy *"
            suffix={
              <Tooltip title="WiFi 802.11ac , Bluetooth 4.2">
                <InfoCircleOutlined style={{ color: suffixColor }} />
              </Tooltip>
            }
          />
        </Form.Item>
      </Col>
      {/* H iu hnh */}
      <Col span={12} md={8} xl={6} xxl={4}>
        <Form.Item
          name="operating"
          rules={[{ required: true, message: "Bt buc", whitespace: true }]}
        >
          <Input
            size="large"
            placeholder="H iu hnh *"
            suffix={
              <Tooltip title="Windows 10 Pro 64-bit">
                <InfoCircleOutlined style={{ color: suffixColor }} />
              </Tooltip>
            }
          />
        </Form.Item>
      </Col>
      {/* Bn phm */}
      <Col span={12} md={8} xl={6} xxl={4}>
        <Form.Item
          name="keyborad"
          rules={[{ required: true, message: "Bt buc", whitespace: true }]}
        >
          <Input
            size="large"
            placeholder="Bn phm *"
            suffix={
              <Tooltip title="thng , khng phim s , LED">
                <InfoCircleOutlined style={{ color: suffixColor }} />
              </Tooltip>
            }
          />
        </Form.Item>
      </Col>
      {/* Pin */}
      <Col span={12} md={8} xl={6} xxl={4}>
        <Form.Item
          name="pin"
          rules={[{ required: true, message: "Bt buc", whitespace: true }]}
        >
          <Input
            size="large"
            placeholder="Dung lng pin *"
            suffix={
              <Tooltip title="4 cell 84 Wh Pin lin">
                <InfoCircleOutlined style={{ color: suffixColor }} />
              </Tooltip>
            }
          />
        </Form.Item>
      </Col>
      {/* kch thc  */}
      <Col span={12} md={8} xl={6} xxl={4}>
        <Form.Item
          name="size"
          rules={[{ required: true, message: "Bt buc", whitespace: true }]}
        >
          <Input
            size="large"
            placeholder="Kch thc *"
            suffix={
              <Tooltip title="39.4 x 26.4 x 1.99 cm">
                <InfoCircleOutlined style={{ color: suffixColor }} />
              </Tooltip>
            }
          />
        </Form.Item>
      </Col>
      {/* Khi lng */}
      <Col span={12} md={8} xl={6} xxl={4}>
        <Form.Item
          name="weight"
          rules={[{ required: true, message: "Bt buc" }]}
        >
          <InputNumber
            style={{ width: "100%" }}
            step={1}
            size="large"
            min={0}
            max={10}
            placeholder="Khi lng *"
          />
        </Form.Item>
      </Col>
    </Row>
  );
}

export default Laptop;
