import { InfoCircleOutlined } from "@ant-design/icons";
import { Col, Form, Input, InputNumber, Row, Select, Tooltip } from "antd";
import React from "react";

const suffixColor = "#aaa";
const BG_PLATES = [
  { type: 0, label: "IPS" },
  { type: 1, label: "VA" },
  { type: 2, label: "TN" },
  { type: 3, label: "PLS" },
  { type: 4, label: "MVA" },
  { type: 5, label: "KHT" },
];
const MONITOR_RESOLUTIONS = [
  { type: 0, label: "1920x1080" },
  { type: 1, label: "2560x1440" },
  { type: 2, label: "1920x1200" },
  { type: 3, label: "1366x768" },
  { type: 4, label: "1600x900" },
  { type: 5, label: "3840x2160" },
  { type: 6, label: "2560x1080" },
  { type: 7, label: "3440x1440" },
];
const TYPEMONITOR_TYPES=[
  { type: 0, label: "Mn hnh phng" },
  { type: 1, label: "Mn hnh cong" },
]

function Monitor() {
  return (
    <Row gutter={[16, 16]}>
      {/* Kch thc mn hnh */}
      <Col span={12} md={8} xl={6} xxl={4}>
        <Form.Item
          name="displaySize"
          rules={[{ required: true, message: "Bt buc", whitespace: true }]}
        >
          <Input
            maxLength={5}
            size="large"
            placeholder="Kch thc mn hnh *"
            suffix={
              <Tooltip title={`24.7"`}>
                <InfoCircleOutlined style={{ color: suffixColor }} />
              </Tooltip>
            }
          />
        </Form.Item>
      </Col>
      {/*  phn gii */}
      <Col span={12} md={8} xl={6} xxl={4}>
        <Form.Item
          name="resolution"
          rules={[{ required: true, message: "Bt buc" }]}
        >
          <Select size="large" placeholder=" phn gii *">
            {MONITOR_RESOLUTIONS.map((item, index) => (
              <Select.Option value={item.type} key={index}>
                {item.label}
              </Select.Option>
            ))}
          </Select>
        </Form.Item>
      </Col>
      {/* Tm nn */}
      <Col span={12} md={8} xl={6} xxl={4}>
        <Form.Item
          name="bgPlate"
          rules={[{ required: true, message: "Bt buc" }]}
        >
          <Select size="large" placeholder="Tm nn *">
            {BG_PLATES.map((item, index) => (
              <Select.Option value={item.type} key={index}>
                {item.label}
              </Select.Option>
            ))}
          </Select>
        </Form.Item>
      </Col>
      {/* Tn s qut */}
      <Col span={12} md={8} xl={6} xxl={4}>
        <Form.Item
          name="frequency"
          rules={[{ required: true, message: "Bt buc" }]}
        >
          <InputNumber
            style={{ width: "100%" }}
            step={30}
            size="large"
            min={0}
            max={1000}
            placeholder="Tn s qut *"
          />
        </Form.Item>
      </Col>
       {/* Thi gian phn hi */}
       <Col span={12} md={8} xl={6} xxl={4}>
        <Form.Item
          name="timeResponse"
          rules={[{ required: true, message: "Bt buc" }]}
        >
          <InputNumber
            style={{ width: "100%" }}
            step={1}
            size="large"
            min={0}
            max={10}
            placeholder="Thi gian phn hi *"
          />
        </Form.Item>
      </Col>
      {/* Kiu man hinh */}
      <Col span={12} md={8} xl={6} xxl={4}>
        <Form.Item
          name="typeMonitor"
          rules={[{ required: true, message: "Bt buc" }]}
        >
          <Select size="large" placeholder="Kiu man hinh *">
            {TYPEMONITOR_TYPES.map((item, index) => (
              <Select.Option value={item.type} key={index}>
                {item.label}
              </Select.Option>
            ))}
          </Select>
        </Form.Item>
      </Col>
       {/*  sng */}
       <Col span={12} md={8} xl={6} xxl={4}>
        <Form.Item
          name="Brightness"
          rules={[{ required: true, message: "Bt buc" }]}
        >
          <InputNumber
            style={{ width: "100%" }}
            step={10}
            size="large"
            min={0}
            max={500}
            placeholder=" sng *"
          />
        </Form.Item>
      </Col>
      {/* Kh nng hin th mu */}
      <Col span={12} md={8} xl={6} xxl={4}>
        <Form.Item
          name="colorVisibility"
          rules={[{ required: true, message: "Bt buc", whitespace: true }]}
        >
          <Input
            size="large"
            placeholder="Kh nng hin th mu *"
            suffix={
              <Tooltip title="16.7 triu mu">
                <InfoCircleOutlined style={{ color: suffixColor }} />
              </Tooltip>
            }
          />
        </Form.Item>
      </Col>
      {/* Cc cng kt ni */}
      <Col span={12} md={8} xl={6} xxl={4}>
        <Form.Item
          name="connectionPort"
          rules={[{ required: true, message: "Bt buc", whitespace: true }]}
        >
          <Input
            size="large"
            placeholder="Cc cng kt ni *"
            suffix={
              <Tooltip title="1 x HDMI, 1 x DVI-D, 1 x VGA/D-sub">
                <InfoCircleOutlined style={{ color: suffixColor }} />
              </Tooltip>
            }
          />
        </Form.Item>
      </Col>
    </Row>
  );
}

export default Monitor;
