import { InfoCircleOutlined } from "@ant-design/icons";
import { Col, Form, Input, InputNumber, Row, Select, Tooltip } from "antd";
import React from "react";
const BANDWIDTHS = [
  { type: 0, label: "2.4 GHz" },
  { type: 1, label: "2.4 GHz/ 5GHz" },
  { type: 2, label: "2.5 GHz/ 5GHz" },
];
const suffixColor = "#aaa";

function Router() {
  return (
    <Row gutter={[16, 16]}>
      {/* Bng thng */}
      <Col span={12} md={8} xl={6} xxl={4}>
        <Form.Item
          name="bandwidth"
          rules={[{ required: true, message: "Bt buc" }]}
        >
          <Select size="large" placeholder="Bng thng *">
            {BANDWIDTHS.map((item, index) => (
              <Select.Option value={item.type} key={index}>
                {item.label}
              </Select.Option>
            ))}
          </Select>
        </Form.Item>
      </Col>
      {/*  mnh ng ten */}
      <Col span={12} md={8} xl={6} xxl={4}>
        <Form.Item
          name="strong"
          rules={[{ required: true, message: "Bt buc" }]}
        >
          <InputNumber
            style={{ width: "100%" }}
            step={1}
            size="large"
            min={0}
            max={20}
            placeholder=" mnh ng ten *"
          />
        </Form.Item>
      </Col>
      {/* Chun kt ni */}
      <Col span={12} md={8} xl={6} xxl={4}>
        <Form.Item
          name="connectionStd"
          rules={[{ required: true, message: "Bt buc", whitespace: true }]}
        >
          <Input
            size="large"
            placeholder="Chun kt ni *"
            suffix={
              <Tooltip title="802.11 b/g/n/ac">
                <InfoCircleOutlined style={{ color: suffixColor }} />
              </Tooltip>
            }
          />
        </Form.Item>
      </Col>
      {/*  cng kt ni */}
      <Col span={12} md={8} xl={6} xxl={4}>
        <Form.Item
          name="numberOfPort"
          rules={[{ required: true, message: "Bt buc", whitespace: true }]}
        >
          <Input
            size="large"
            placeholder="Cc cng kt ni *"
            suffix={
              <Tooltip title="1xWAN Gigabit">
                <InfoCircleOutlined style={{ color: suffixColor }} />
              </Tooltip>
            }
          />
        </Form.Item>
      </Col>
      {/*  ng tn */}
      <Col span={12} md={8} xl={6} xxl={4}>
        <Form.Item
          name="Antenna"
          rules={[{ required: true, message: "Bt buc", whitespace: true }]}
        >
          <Input
            size="large"
            placeholder="Cc ng tn *"
            suffix={
              <Tooltip title="3x ngi">
                <InfoCircleOutlined style={{ color: suffixColor }} />
              </Tooltip>
            }
          />
        </Form.Item>
      </Col>
      {/* Tc  */}
      <Col span={12} md={8} xl={6} xxl={4}>
        <Form.Item
          name="speed"
          rules={[{ required: true, message: "Bt buc", whitespace: true }]}
        >
          <Input
            size="large"
            placeholder="Tc  *"
            suffix={
              <Tooltip title="2.4GHz - 450Mbps">
                <InfoCircleOutlined style={{ color: suffixColor }} />
              </Tooltip>
            }
          />
        </Form.Item>
      </Col>

      {/*  CPU/RAM/Flash */}
      <Col span={12} md={8} xl={6} xxl={4}>
        <Form.Item
          name="cpuRamFlash"
          rules={[{ required: true, message: "Bt buc", whitespace: true }]}
        >
          <Input
            size="large"
            placeholder="CPU/RAM/Flash *"
            suffix={
              <Tooltip title="RAM 512MB / Flash 256MB">
                <InfoCircleOutlined style={{ color: suffixColor }} />
              </Tooltip>
            }
          />
        </Form.Item>
      </Col>
      {/*  Ngun in cp */}
      <Col span={12} md={8} xl={6} xxl={4}>
        <Form.Item
          name="voltage"
          rules={[{ required: true, message: "Bt buc", whitespace: true }]}
        >
          <Input
            size="large"
            placeholder="Ngun in cp *"
            suffix={
              <Tooltip title="12V DC / 1.5A">
                <InfoCircleOutlined style={{ color: suffixColor }} />
              </Tooltip>
            }
          />
        </Form.Item>
      </Col>
      {/*  Tnh nng khc */}
      <Col span={12} md={8} xl={6} xxl={4}>
        <Form.Item name="otherFunc">
          <Input
            size="large"
            placeholder="Tnh nng khc *"
            suffix={
              <Tooltip title="">
                <InfoCircleOutlined style={{ color: suffixColor }} />
              </Tooltip>
            }
          />
        </Form.Item>
      </Col>
      {/*  Ph kin i km */}
      <Col span={12} md={8} xl={6} xxl={4}>
        <Form.Item name="accessories">
          <Input
            size="large"
            placeholder="Ph kin i km *"
            suffix={
              <Tooltip title="">
                <InfoCircleOutlined style={{ color: suffixColor }} />
              </Tooltip>
            }
          />
        </Form.Item>
      </Col>
      {/*  kch thc */}
      <Col span={12} md={8} xl={6} xxl={4}>
        <Form.Item name="size">
          <Input
            size="large"
            placeholder="Kch thc *"
            suffix={
              <Tooltip title="">
                <InfoCircleOutlined style={{ color: suffixColor }} />
              </Tooltip>
            }
          />
        </Form.Item>
      </Col>
    </Row>
  );
}

export default Router;
