import { Col, Form, Input, InputNumber, Row, Select, Tooltip } from "antd";
import React from "react";
import { InfoCircleOutlined } from "@ant-design/icons";

const suffixColor = "#aaa";

const MOUSE_TYPES = [
  { type: 0, label: "C dy" },
  { type: 1, label: "Khng dy" },
];

const MOUSE_LEDS = [
  { type: true, label: "C led" },
  { type: false, label: "Khng Led" },
];

const COLOR_TYPES = [
  { type: 0, label: "en" },
  { type: 1, label: "Bc" },
  { type: 2, label: "Trng" },
  { type: 3, label: "Hng" },
  { type: 4, label: "" },
  { type: 5, label: "Xm" },
];

const TYPESENSOR_TYPES = [
  { type: 0, label: "Optical" },
  { type: 1, label: "Laser" },
];

function Mouse() {
  return (
    <Row gutter={[16, 16]}>
      {/* Loi Chut */}
      <Col span={12} md={8} xl={6} xxl={4}>
        <Form.Item
          name="typeConnect"
          rules={[{ required: true, message: "Bt buc" }]}
        >
          <Select size="large" placeholder="Loi Chut *">
            {MOUSE_TYPES.map((item, index) => (
              <Select.Option value={item.type} key={index}>
                {item.label}
              </Select.Option>
            ))}
          </Select>
        </Form.Item>
      </Col>
      {/* n Led */}
      <Col span={12} md={8} xl={6} xxl={4}>
        <Form.Item
          name="isLed"
          rules={[{ required: true, message: "Bt buc" }]}
        >
          <Select size="large" placeholder="n Led *">
            {MOUSE_LEDS.map((item, index) => (
              <Select.Option value={item.type} key={index}>
                {item.label}
              </Select.Option>
            ))}
          </Select>
        </Form.Item>
      </Col>
      {/* Mu sc */}
      <Col span={12} md={8} xl={6} xxl={4}>
        <Form.Item
          name="color"
          rules={[{ required: true, message: "Bt buc" }]}
        >
          <Select size="large" placeholder="Mu sc *">
            {COLOR_TYPES.map((item, index) => (
              <Select.Option value={item.type} key={index}>
                {item.label}
              </Select.Option>
            ))}
          </Select>
        </Form.Item>
      </Col>
      {/* kt ni */}
      <Col span={12} md={8} xl={6} xxl={4}>
        <Form.Item
          name="connect"
          rules={[{ required: true, message: "Bt buc", whitespace: true }]}
        >
          <Input
            size="large"
            placeholder="Kt ni *"
            suffix={
              <Tooltip title="USB 2.0, Wireless">
                <InfoCircleOutlined style={{ color: suffixColor }} />
              </Tooltip>
            }
          />
        </Form.Item>
      </Col>
      {/*  phn giai (CPI/DPI) */}
      <Col span={12} md={8} xl={6} xxl={4}>
        <Form.Item
          name="resolution"
          rules={[{ required: true, message: "Bt buc" }]}
        >
          <Input
            size="large"
            placeholder=" phn gii *"
            suffix={
              <Tooltip title={`12000DPI`}>
                <InfoCircleOutlined style={{ color: suffixColor }} />
              </Tooltip>
            }
          />
        </Form.Item>
      </Col>
      {/* Dang cam bin */}
      <Col span={12} md={8} xl={6} xxl={4}>
        <Form.Item
          name="typeSensor"
          rules={[{ required: true, message: "Bt buc" }]}
        >
          <Select size="large" placeholder="Dang cam bin *">
            {TYPESENSOR_TYPES.map((item, index) => (
              <Select.Option value={item.type} key={index}>
                {item.label}
              </Select.Option>
            ))}
          </Select>
        </Form.Item>
      </Col>
      {/* Tn cm bin  */}
      <Col span={12} md={8} xl={6} xxl={4}>
        <Form.Item name="nameSensor">
          <Input
            size="large"
            placeholder="Tn cm bin *"
            suffix={
              <Tooltip title={`PMW3327`}>
                <InfoCircleOutlined style={{ color: suffixColor }} />
              </Tooltip>
            }
          />
        </Form.Item>
      </Col>
      {/* S nt bm  */}
      <Col span={12} md={8} xl={6} xxl={4}>
        <Form.Item
          name="numOfButton"
          rules={[{ required: true, message: "Bt buc" }]}
        >
          <InputNumber
            style={{ width: "100%" }}
            step={1}
            size="large"
            min={0}
            max={10}
            placeholder="S nt bm *"
          />
        </Form.Item>
      </Col>
      {/* Kch thc  */}
      <Col span={12} md={8} xl={6} xxl={4}>
        <Form.Item
          name="size"
          rules={[{ required: true, message: "Bt buc", whitespace: true }]}
        >
          <Input
            size="large"
            placeholder="Kch thc *"
            suffix={
              <Tooltip title={`10.75 x 5.92 x 3.83"`}>
                <InfoCircleOutlined style={{ color: suffixColor }} />
              </Tooltip>
            }
          />
        </Form.Item>
      </Col>
    </Row>
  );
}

export default Mouse;
