import { InfoCircleOutlined } from "@ant-design/icons";
import { Col, Form, Input, Select, InputNumber, Row, Tooltip } from "antd";
import React from "react";
const suffixColor = "#aaa";

const COLOR_TYPES = [
  { type: 0, label: "en" },
  { type: 1, label: "Bc" },
  { type: 2, label: "Trng" },
  { type: 3, label: "Hng" },
  { type: 4, label: "" },
  { type: 5, label: "Xm" },
  { type: 6, label: "Xanh" },
  { type: 7, label: "Vng" },
  { type: 8, label: "Cam" },
];

function Speaker() {
  return (
    <Row gutter={[16, 16]}>
      {/* Mu sc */}
      <Col span={12} md={8} xl={6} xxl={4}>
        <Form.Item
          name="color"
          rules={[{ required: true, message: "Bt buc" }]}
        >
          <Select size="large" placeholder="Mu sc *">
            {COLOR_TYPES.map((item, index) => (
              <Select.Option value={item.type} key={index}>
                {item.label}
              </Select.Option>
            ))}
          </Select>
        </Form.Item>
      </Col>
      {/* Cng sut (W) */}
      <Col span={12} md={8} xl={6} xxl={4}>
        <Form.Item
          name="wattage"
          rules={[{ required: true, message: "Bt buc" }]}
        >
          <InputNumber
            style={{ width: "100%" }}
            step={10}
            size="large"
            min={0}
            max={10000}
            placeholder="Cng sut (W) *"
          />
        </Form.Item>
      </Col>
      {/* Lai kt ni ca loa */}
      <Col span={12} md={8} xl={6} xxl={4}>
        <Form.Item
          name="typeConnect"
          rules={[{ required: true, message: "Bt buc" }]}
        >
          <Input
            size="large"
            placeholder="Lai kt ni *"
            suffix={
              <Tooltip title="loa khng dy">
                <InfoCircleOutlined style={{ color: suffixColor }} />
              </Tooltip>
            }
          />
        </Form.Item>
      </Col>
      {/*  Cng kt ni */}
      <Col span={12} md={8} xl={6} xxl={4}>
        <Form.Item
          name="connectionPort"
          rules={[{ required: true, message: "Bt buc" }]}
        >
          <Input
            size="large"
            placeholder="Cng kt ni *"
            suffix={
              <Tooltip title="3.5 mm">
                <InfoCircleOutlined style={{ color: suffixColor }} />
              </Tooltip>
            }
          />
        </Form.Item>
      </Col>
      {/*  kiu pin */}
      <Col span={12} md={8} xl={6} xxl={4}>
        <Form.Item
          name="typePin"
          rules={[{ required: true, message: "Bt buc" }]}
        >
          <Input
            size="large"
            placeholder="Kiu pin *"
            suffix={
              <Tooltip title="Pin Lithium">
                <InfoCircleOutlined style={{ color: suffixColor }} />
              </Tooltip>
            }
          />
        </Form.Item>
      </Col>
      {/*  Tn s phn hi */}
      <Col span={12} md={8} xl={6} xxl={4}>
        <Form.Item
          name="fbfrequency"
          rules={[{ required: true, message: "Bt buc" }]}
        >
          <Input
            size="large"
            placeholder="Tn s phn hi *"
            suffix={
              <Tooltip title="60Hz20kHz">
                <InfoCircleOutlined style={{ color: suffixColor }} />
              </Tooltip>
            }
          />
        </Form.Item>
      </Col>
       {/*  Tnh nng khc */}
       <Col span={12} md={8} xl={6} xxl={4}>
        <Form.Item name="otherFunc">
          <Input
            size="large"
            placeholder="Tnh nng khc *"
            suffix={
              <Tooltip title="">
                <InfoCircleOutlined style={{ color: suffixColor }} />
              </Tooltip>
            }
          />
        </Form.Item>
      </Col>
       {/*  kch thc */}
       <Col span={12} md={8} xl={6} xxl={4}>
        <Form.Item name="size">
          <Input
            size="large"
            placeholder="Kch thc *"
            suffix={
              <Tooltip title="">
                <InfoCircleOutlined style={{ color: suffixColor }} />
              </Tooltip>
            }
          />
        </Form.Item>
      </Col>
    </Row>
  );
}
export default Speaker;
