import { Col, Form, Input, Row, Select, Tooltip } from "antd";
import { InfoCircleOutlined } from "@ant-design/icons";
import React from "react";

const suffixColor = "#aaa";

const HEADPHONE_TYPES = [
  { type: 0, label: "Over-ear" },
  { type: 1, label: "In-ear" },
  { type: 2, label: "On-ear" },
  { type: 3, label: "KHT" },
];
const COLOR_TYPES = [
  { type: 0, label: "en" },
  { type: 1, label: "Bc" },
  { type: 2, label: "Trng" },
  { type: 3, label: "Hng" },
  { type: 4, label: "" },
  { type: 5, label: "Xm" },
  { type: 6, label: "Xanh" },
  { type: 7, label: "Vng" },
];
const CONNECT_TYPES = [
  { type: 0, label: "Tai nghe khng dy" },
  { type: 1, label: "Tai nghe c dy" },
];
// const CONNECTION_STD = [
//   { type: 0, label: "3.5mm" },
//   { type: 1, label: "bluetooth" },
//   { type: 2, label: "USB" },
//   { type: 3, label: "bluetooth 4.0" },
//   { type: 4, label: "bluetooth 5.0" },
//   { type: 5, label: "2.4GHz Wireless" },
//   { type: 6, label: "USB Type-C" },
// ];
const MICROPHONE_TYPES = [
  { type: 0, label: "Khng" },
  { type: 1, label: "C" },
];

function Headphone() {
  return (
    <Row gutter={[16, 16]}>
      {/* Loi tai nghe */}
      <Col span={12} md={8} xl={6} xxl={4}>
        <Form.Item
          name="type"
          rules={[{ required: true, message: "Bt buc" }]}
        >
          <Select size="large" placeholder="Loi tai nghe *">
            {HEADPHONE_TYPES.map((item, index) => (
              <Select.Option value={item.type} key={index}>
                {item.label}
              </Select.Option>
            ))}
          </Select>
        </Form.Item>
      </Col>
      {/* Mu sc */}
      <Col span={12} md={8} xl={6} xxl={4}>
        <Form.Item
          name="color"
          rules={[{ required: true, message: "Bt buc" }]}
        >
          <Select size="large" placeholder="Mu sc *">
            {COLOR_TYPES.map((item, index) => (
              <Select.Option value={item.type} key={index}>
                {item.label}
              </Select.Option>
            ))}
          </Select>
        </Form.Item>
      </Col>
      {/* Kt ni  */}
      <Col span={12} md={8} xl={6} xxl={4}>
        <Form.Item
          name="connect"
          rules={[{ required: true, message: "Bt buc" }]}
        >
          <Input
            size="large"
            placeholder="Kt ni *"
            suffix={
              <Tooltip title="USB 2.0, Wireless">
                <InfoCircleOutlined style={{ color: suffixColor }} />
              </Tooltip>
            }
          />
        </Form.Item>
      </Col>
      {/* Loi kt ni  */}
      <Col span={12} md={8} xl={6} xxl={4}>
        <Form.Item
          name="typeConnect"
          rules={[{ required: true, message: "Bt buc" }]}
        >
          <Select size="large" placeholder="Loi kt ni *">
            {CONNECT_TYPES.map((item, index) => (
              <Select.Option value={item.type} key={index}>
                {item.label}
              </Select.Option>
            ))}
          </Select>
        </Form.Item>
      </Col>
      {/* n Leb  */}
      <Col span={12} md={8} xl={6} xxl={4}>
        <Form.Item name="isLed">
          <Input
            size="large"
            placeholder="n Leb *"
            suffix={
              <Tooltip title="RGB">
                <InfoCircleOutlined style={{ color: suffixColor }} />
              </Tooltip>
            }
          />
        </Form.Item>
      </Col>
      {/* Microphone  */}
      <Col span={12} md={8} xl={6} xxl={4}>
        <Form.Item
          name="microphone"
          rules={[{ required: true, message: "Bt buc" }]}
        >
          <Select size="large" placeholder="Microphone  *">
            {MICROPHONE_TYPES.map((item, index) => (
              <Select.Option value={item.type} key={index}>
                {item.label}
              </Select.Option>
            ))}
          </Select>
        </Form.Item>
      </Col>
    </Row>
  );
}

export default Headphone;
