import { InfoCircleOutlined } from "@ant-design/icons";
import { Col, Form, Input, InputNumber, Row, Tooltip } from "antd";
import React from "react";
const suffixColor = "#aaa";

function BackupCharger() {
  return (
    <Row gutter={[16, 16]}>
      {/* Dung lng pin */}
      <Col span={12} md={8} xl={6} xxl={4}>
        <Form.Item
          name="capacityPin"
          rules={[{ required: true, message: "Bt buc" }]}
        >
          <InputNumber
            style={{ width: "100%" }}
            step={2000}
            size="large"
            min={0}
            max={100000}
            placeholder="Dung lng (mAh) *"
          />
        </Form.Item>
      </Col>
      {/* Mu sc */}
      <Col span={12} md={8} xl={6} xxl={4}>
        <Form.Item
          name="color"
          rules={[{ required: true, message: "Bt buc", whitespace: true }]}
        >
          <Input
            size="large"
            placeholder="Mu sc *"
            suffix={
              <Tooltip title="">
                <InfoCircleOutlined style={{ color: suffixColor }} />
              </Tooltip>
            }
          />
        </Form.Item>
      </Col>
      {/* Li pin */}
      <Col span={12} md={8} xl={6} xxl={4}>
        <Form.Item
          name="corePin"
          rules={[{ required: true, message: "Bt buc", whitespace: true }]}
        >
          <Input
            size="large"
            placeholder="Li pin *"
            suffix={
              <Tooltip title="Lithium Polymer">
                <InfoCircleOutlined style={{ color: suffixColor }} />
              </Tooltip>
            }
          />
        </Form.Item>
      </Col>
      {/* S cng sc */}
      <Col span={12} md={8} xl={6} xxl={4}>
        <Form.Item
          name="numberOfPort"
          rules={[{ required: true, message: "Bt buc" }]}
        >
          <InputNumber
            style={{ width: "100%" }}
            step={1}
            size="large"
            min={1}
            max={8}
            placeholder="S cng sc *"
          />
        </Form.Item>
      </Col>

      {/* Mc in p vao*/}
      <Col span={12} md={8} xl={6} xxl={4}>
        <Form.Item
          name="voltageIn"
          rules={[{ required: true, message: "Bt buc", whitespace: true }]}
        >
          <Input
            size="large"
            placeholder="Mc in p vo*"
            suffix={
              <Tooltip title="5V/1A">
                <InfoCircleOutlined style={{ color: suffixColor }} />
              </Tooltip>
            }
          />
        </Form.Item>
      </Col>
      {/* Mc in p ra*/}
      <Col span={12} md={8} xl={6} xxl={4}>
        <Form.Item
          name="voltageOut"
          rules={[{ required: true, message: "Bt buc", whitespace: true }]}
        >
          <Input
            size="large"
            placeholder="Mc in p ra*"
            suffix={
              <Tooltip title="5V/1A, 2A">
                <InfoCircleOutlined style={{ color: suffixColor }} />
              </Tooltip>
            }
          />
        </Form.Item>
      </Col>
      {/* Khi lng (g) */}
      <Col span={12} md={8} xl={6} xxl={4}>
        <Form.Item
          name="weightG"
          rules={[{ required: true, message: "Bt buc" }]}
        >
          <InputNumber
            style={{ width: "100%" }}
            step={100}
            size="large"
            min={0}
            max={10000}
            placeholder="Khi lng (g) *"
          />
        </Form.Item>
      </Col>
      {/* Kch thc*/}
      <Col span={12} md={8} xl={6} xxl={4}>
        <Form.Item
          name="size"
          rules={[{ required: true, message: "Bt buc" }]}
        >
          <Input
            size="large"
            placeholder="Kch thc*"
            suffix={
              <Tooltip title="138 x 69 x 14 mm">
                <InfoCircleOutlined style={{ color: suffixColor }} />
              </Tooltip>
            }
          />
        </Form.Item>
      </Col>
    </Row>
  );
}

export default BackupCharger;
